@import './style-variables.css';

:root {
  --modalheight: 82vh;
  --modalwidth: 85vw;
}

.movie-card {
  display: flex;
  justify-content: center;
  padding: 2vh 0px;
  z-index: 10;
  position: fixed;
  width: 100%;
  height: 100%;
  font-family: var(--fontfamily);
  color: var(--midgrey);
  background-color: rgba(35, 38, 46, 0.6);
}

/* ======================================================= */
/* Backdrop */

.movie-card .backdrop {
  border-radius: 20px;
  height: var(--modalheight);
  width: var(--modalwidth);
  position: absolute;
  z-index: -1;
  overflow: hidden;
  background-color: var(--purple);
}

.movie-card .backdrop img {
  width: 100%;
  z-index: -2;
  border-radius: 20px;
  position: absolute;
}

.movie-card .backdrop .overlay {
  background-color: var(--darkgrey);
  opacity: 0.5;
  height: 100%;
  width: 100%;
  position: absolute;
}

/* ======================================================= */
/* ======================================================= */
/* Info section */

.movie-card .info {
  border-radius: 20px;
  background: linear-gradient(to top, var(--offwhite) 50%, transparent 100%);
  position: absolute;
  height: var(--modalheight);
  width: var(--modalwidth);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* ======================================================= */
/* Info Heading */

.movie-card .info .heading {
  margin-top: 10%;
  position: relative;
  padding: 25px;
  padding-bottom: 5px;
  height: 30%;
}

.movie-card .heading .poster {
  position: relative;
  float: left;
  margin-right: 20px;
  height: 100%;
  box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.5);
}

.movie-card .heading h1 {
  color: var(--darkgrey);
  font-weight: 500;
  margin: 0;
  /* font-size: 2.7vw; */
}

.movie-card .heading h4 {
  font-weight: 400;
  margin: 0;
  /* font-size: 2vw; */
}

.movie-card .heading .genres {
  display: inline-block;
  margin-left: 10px;
  color: var(--lightgrey);
  /* font-size: 1.5vw; */
}

.movie-card .heading .runtime {
  display: inline-block;
  /* margin-top: 15px; */
  padding: 5px;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

/* ======================================================= */
/* Info Description */

.movie-card .description {
  color: var(--darkgrey);
  padding: 10px 25px;
  overflow: hidden;
  height: 40%;
}

.movie-card .description p {
  margin: 0;
  /* font-size: 1.8vw; */
}

/* ======================================================= */
/* Info Ratings */

.movie-card #line2 {
  display: flex;
  height: 1.4em;
}

.movie-card #ratings {
  display: flex;
  justify-content: space-evenly;
  margin-left: 10px;
  font-size: 2.8vh;
  overflow: hidden;
}

.movie-card #ratings li {
  list-style: none;
  padding: 4px;
}

.movie-card #ratings li img {
  height: 70%;
  float: left;
  margin: 3px 6px 2px 2px;
}

.movie-card #ratings li a {
  text-decoration: none;
  color: var(--darkgrey);
}

/* ======================================================= */
/* Info Streams */

.movie-card #streams {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 12%;
  margin: 5px 25px;
}

.movie-card #streams h3 {
  margin: 0;
  font-size: 3.5vh;
}

.movie-card #streams ul {
  margin: 0;
  margin-left: 10px;
  padding: 0;
  display: flex;
  align-items: center;
  list-style: none;
  height: 100%;
}

.movie-card #streams li {
  margin: 3px 10px;
}

.movie-card #streams li a {
  align-items: baseline;
  display: flex;
  height: 100%;
}

.movie-card #streams li img {
  height: 18px;
  /* width: 18px; */
}

/* ======================================================= */
/* Close Button */

.movie-card .close-modal {
  position: absolute;
  cursor: pointer;
  right: 0px;
  z-index: 1;
  border-radius: 20px;
  width: 30px;
  height: 30px;
}

